import React from "react";

export default class StickerFilterView extends React.Component {
    constructor(props) {
        super(props);
    }

    toHTML(filterName) {
        return {__html: filterName};
    }

    componentDidMount() {
    }

    componentDidUpdate(){
        if(document.getElementById("opl-sticker-expander-main" + this.props.isModal) != null && !document.getElementById("opl-sticker-expander-main"+ this.props.isModal).hasAttribute("data-js-processed")) {
            OPL.UI.initModules($('#ora-product-sticker-filter-component')[0]);
        }
    }

    render() {
        return !this.props.filterConfiguration.stickerFilter || this.props.filterConfiguration.stickerFilter.length == 0 ? null : (
            <div id={"opl-sticker-expanders" + this.props.isModal}>
                <div id={"opl-sticker-expander-main" + this.props.isModal}
                     data-js-module="common/modules/opl-expander"
                     data-js-options={JSON.stringify({
                         scrollToSelected: false, triggerSelector: ".opl-section__trigger",
                         "contentSelector": ".opl-section__content", "parentClass": "opl-section"
                     })}
                     role="tablist" aria-multiselectable="false">

                    <div className={"opl-section u-no-padding" + (this.props.initiallyExpanded? ' is-expanded' : '')}
                         aria-expanded={(this.props.initiallyExpanded? 'true' : 'false')}>
                        <div className="opl-section__header u-no-padding">
                            <div className="o-separator u-spacing-l"></div>
                            <div className="l-row">
                                <div className="l-col l-col-9 ">
                                    <p className="h4 u-spacing-l">{this.props.title}</p>
                                </div>
                                <div className="l-col l-col-3 ">
                                    <a href="#"
                                       className="u-text-right u-block opl-section__trigger opl-wiking-expander__trigger--plus"
                                       role="tab">
                                        <span className="g-icon g-icon--only g-icon--plus g-icon--xs"></span>
                                        <span className="u-acc-hide">{"Rozwiń sekcję " + this.props.title}</span>
                                    </a>
                                </div>
                            </div>
                        </div>

                        <div className="opl-section__content u-hide--soft"
                             role="tabpanel">
                            <div className="l-row">
                                <div className="l-col l-col-12 ">
                                    <div className="u-padding-right-xl">
                                        <ul className="opl-wiking-manufacturers">
                                            {this.props.filterConfiguration.stickerFilter.map((pos, index) => {
                                                return this.renderAttribute(pos);
                                            })}
                                        </ul>
                                    </div>
                                </div>
                            </div>

                            <div className="u-clear-both"></div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    renderAttribute(element){
        return (<ul className="u-spacing-l">
            {element.type=='enum'?element.availableValues.map((pos, index) => {
               return element.multiValue?this.renderCuteLinkIfAvailable(pos, element):this.renderValueRadio(pos, element);
            }):''}
        </ul>);
    }

    renderCuteLinkIfAvailable(element, parent) {
        if (parent.cuteName !== "" && !this.getStateOfCheckboxes(parent.code,element)) {
            return (<a href={"/sklep/" + parent.cuteName}> {this.renderValueCheckBox(element, parent)} </a>);
        } else {
            return this.renderValueCheckBox(element, parent);
        }
    }

    renderValueCheckBox(element, parent) {
        return (
            <li className="u-spacing">
                <label className="o-checkbox opl-checkbox">
                    <input type="checkbox"
                           name={this.props.component + "_" + parent.code}
                           onClick={(e,checked) => this.changeAttribute(element, parent, true, "add", checked)}
                           checked={this.getStateOfCheckboxes(parent.code,element)} />
                    <span className="o-ci"/>
                    <span className="o-ci-label u-font-normal">{this.stripHtmlTagsFromString(parent.name)}&nbsp;
                        <span className="g-gray4-c u-font-small">({parent.unit})</span>
                    </span>
                </label>
            </li>
        );
    }

    getStateOfCheckboxes(name,value) {
        var values = this.props.selectedStickerFilters[name];
        if(values){
            return (values.indexOf(value) != -1);
        }
        return false;
    }

     stripHtmlTagsFromString(text) {
        return text.replace(/<\/?[^>]+(>|$)/g, "");
    }

    changeAttribute(element, parent, multiValue, operation, e) {
        this.props.changeStickerFilterAttribute(element, parent, multiValue, operation);
        this.props.reloadTree();
    }

}
